<template>
  <section class="d-flex container position-relative">
    <section class="position-absolute">
      <div v-for="(alert,index) in alertArray" :key="index">
        <div
          class="alert alert-dismissible fade show"
          :class="alert.isDanger?'alert-danger':'alert-primary'"
          role="alert"
        >
          <strong>{{alert.text}}</strong>
          <button type="button" @click.prevent="alertArray.splice(index,1)">
            <span>&times;</span>
          </button>
        </div>
      </div>
    </section>
    <Aside></Aside>
    <router-view v-on:pushAlert="alert"></router-view>
  </section>
</template>
<script>
import Aside from "./components/admin_aside.vue";
import AxiosClass from "@/config/axiosClass.js";
export default {
  data() {
    return {
      alertArray: []
    };
  },
  components: {
    Aside
  },
  name: "admin",
  methods: {
    alert(text,isDanger) {
      this.alertArray.push({ text: text, isDanger: isDanger });
      setTimeout(()=>{this.alertArray.splice(0,1)},3000)
    }
  }
};
</script>
    
